<template>
  <div>
    <form @submit.prevent="handleSubmit" class="contact-form">
        <transition name="slide-fade">
            <first-form v-if="show1" @showSecondForm="showSecondForm"/>
        </transition>
        <transition name="slide-fade" >
            <second-form v-if="show2" @showThirdForm="showThirdForm" />
        </transition>
        <transition name="slide-fade" >
            <third-form v-if="show3" />
        </transition>
    </form>
  </div>
</template>

<script>
import firstForm from './firstForm.vue'
import secondForm from './secondForm.vue'
import thirdForm from './thirdForm.vue'

export default {
  name: 'App',
  components: {
    firstForm,
    secondForm,
    thirdForm
  },
  data(){
      return {
          show1: true,
          show2: false,
          show3: false
      }
  },
  methods: {
      showSecondForm(){
        this.show1 = false;
        setTimeout(()=> {
            this.show2 = true
        }, 1000) 
      },
      showThirdForm(){
        this.show2 = false;
        setTimeout(()=> {
            this.show3 = true
        }, 1000) 
      },
      handleSubmit() {
          console.log('submitted')
      }
  }
  
}
</script>

<style>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active до версии 2.1.8 */ {
  transform: translateX(-10px);
  opacity: 0;
}
</style>
